.NavBar{
    position: fixed;
    z-index: 999;
    opacity: 0.9;
    display: flex;
    justify-content: center;
    align-items: center;
}
.NavBar ul li{
    display: inline-block;
    margin: 20px;
    
}
.NavBar ul li a{
    text-decoration: none;
    align-items: center;
    color:  rgb(54, 28, 36);
}
.NavBar ul {
    padding: 0%;
    margin: 0%;
}
:root{
    --nav-primary-color: #222;
    --nav-text-color:  rgb(54, 28, 36);
    --nav--hov-color:whitesmoke;
}
.NavBar a:hover{
    color: whitesmoke;
}

@media screen and (max-width: 600px) {  
    .NavBar{
        position: fixed;
        z-index: 999;
        opacity: 0.9;
        display: flex;
        background: rosybrown;
        border-bottom-left-radius: 150px 120px;
        margin-left: 10%;
    }
   
    .NavBar ul li{
        display:inline-block;
        margin-top: 0;
        margin-left: 0%;
    }
    
    .NavBar ul li a{
        text-decoration: none;
        display: flex;
        flex-direction: row;
        color:  rgb(92, 9, 34);
    }
    .NavBar ul {
        padding: 0%;
        margin-left: 0%;
    }
    :root{
        --nav-primary-color: #222;
        --nav-text-color:  rgb(54, 28, 36);
        --nav--hov-color:whitesmoke;
    }
    .NavBar a:hover{
        color: #181818;
        opacity: 0.7;
    }  
    
}    