.Binary{
    width: 350px;
    height: 250px;
    padding: 5px;
    border-radius: 50%;
    margin-top: 35px;
    margin-left: 45px;
}
.Content{
    display: flex;
    flex-direction: column;
    width: 47%;
    padding-left: 5%;
}
.Cart .Content{
    padding-top: 13%;
    position: relative;
}
h4{
    line-height: 0%;
}


@media screen and (max-width: 600px) {  
    .Binary{
        width: 400px;
        height: 350px;
        border-radius: 5%;
        margin-left: 3.5%;
    }
    .Content{
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    .Content h1{
        margin-top: 20%;
    }
    h4{
        line-height: 0%;
    }
}    