.About .Container{
    display: flex;
}
.About{
    display: flex;
    flex-direction: column;
    height: 630px;
    padding-top: 120px;
    position: relative;
    margin-left: 8%;
    width: 47%;
}


@media screen and (max-width: 600px) {  
    .About .Container{
        display: flex;
    }
    .About{
        display: flex;
        flex-direction: column;
        height: 630px;
        padding-top: 120px;
        position: relative;
        margin-left: 30%;
        width: 47%;
    }
}    