
	@media screen and (max-width: 480px) {

		html, body {
			min-width: 320px;
		}

        .Home{
            height: 400px;
        }

	}

.Home{
    display: flex;
    flex-direction: column;
    height: 630px;
    padding-top: 100px;
    position: relative;
}
.Home .Container h1{
    font-size: 2rem;
    line-height: 0%;
}
.Home .Container h6{
    line-height: 0%;
}

.Home .Container{
    position: absolute;
    top: 25%;
    left: 13%;
    width: 47%;
}
.Home .Container .Title{
    font-size: 63px;
    color: rgb(54, 28, 36);
}
.Home .Button{
    height: 60px;
    font-size: 25px;
    background: rgb(54, 28, 36);
    color: whitesmoke;
    border-radius: 15px 5px;
    font-weight: bold;
}
.Home .Footer{
    background-color: rosybrown;
    height: 200px;
    width: 100%;
    align-items: center;
    display: flex;
    justify-content: center;
    margin-top: 35%;
    cursor: pointer;
    border-top-left-radius: 100px 40px;
    border-top-right-radius: 100px 40px;
}  
.Footer .Container{
    position: absolute;
    top: 25%;
    left: 13%;
}
.Content{
    flex-direction: column;
}


@media screen and (max-width: 600px) {  
    
.Home .Container h1{
    font-size: 35px;
    line-height: 0%;
}

.Home .Container h6{
    line-height: 0%;
    font-size: 8px;
}

.Home .Container{
    position: absolute;
    top: 25%;
    left: 13%;
    width: 47%;
}
.Home .Container .Title{
    font-size: 1rem;
    font-family: Verdana;
    color: rgb(54, 28, 36);
}
.Home .Button{
    height: 50px;
    font-size: 20px;
    background: rgb(54, 28, 36);
    color: whitesmoke;
    border-radius: 15px 5px;
}
.Home .Footer{
    background-color: rosybrown;
    height: 200px;
    width: 100%;
    align-items: center;
    display: flex;
    justify-content: center;
    margin-top: 100%;
    cursor: pointer;
    border-top-left-radius: 100px 40px;
    border-top-right-radius: 100px 40px;
}  
.Footer .Container{
    position: absolute;
    top: 25%;
    left: 13%;
}
.Content{
    flex-direction: column;
}

}    