.Contact .Icons{
    background-color: rosybrown;
    height: 200px;
    width: 100%;
    margin-top: 35%;
    align-items: center;
    display: flex;
    justify-content: center;
    cursor: pointer;
    border-top-left-radius: 100px 40px;
    border-top-right-radius: 100px 40px;
}  
.Contact{
    display: flex;
    flex-direction: column;
    height: 630px;
    padding-top: 100px;
    position: relative;
}
.Contact .Content{
    position: absolute;
    top: 25%;
    left: 13%;
    width: 47%;
}


@media screen and (max-width: 600px) {  
    .Contact .Icons{
        background-color: rosybrown;
        height: 100px;
        width: 100%;
        margin-top: 120%;
        align-items: center;
        display: flex;
        justify-content: center;
        cursor: pointer;
        border-top-left-radius: 100px 40px;
        border-top-right-radius: 100px 40px;
    }  
    .Contact{
        display: flex;
        flex-direction: column;
        height: 630px;
        position: relative;
    }
    .Contact .Content{
        position: absolute;
        top: 20%;
        left: 30%;
        width: 47%;
    }
}    


